<template>
  <div class="game">
      <header>
        <h1>Snake</h1>
      </header>

      <div class="row center">
        <aside class="col-lg-3">
          <nav>
            <h2>Réglages</h2>
            <div class="row center-xs"  v-if="ready == false">
              <fieldset>
                <legend>Taille de la grille</legend>
                <div class="row">
                  <label for="size-20" class="col-lg-8">Petit</label>
                  <input type="radio" name="size" id="size-20" value="22" v-model="gridSize">
                </div>
                <div class="row">
                  <label for="size-30" class="col-lg-8">Moyen</label>
                  <input type="radio" name="size" id="size-30" value="32" v-model="gridSize">
                </div>
                <div class="row">
                  <label for="size-40" class="col-lg-8">Grand</label>
                  <input type="radio" name="size" id="size-40" value="42" v-model="gridSize">
                </div>
              </fieldset>
              
              <button class="col-lg-3" v-on:click="sendGridSize" v-if="gridSize !== 0 && speed !== 0 ">Générer</button>
            </div>
            <div class="row center-xs"  v-if="ready == false">
              <fieldset>
                <legend>Vitesse</legend>
                <div class="row">
                  <label for="speed-normal" class="col-lg-8">Normale</label>
                  <input type="radio" name="speed" id="speed-normal" value="400" checked v-model="speed">
                </div>
                <div class="row">
                  <label for="speed-fast" class="col-lg-8">Rapide</label>
                  <input type="radio" name="speed" id="speed-fast" value="200" v-model="speed">
                </div>
              </fieldset>
            </div>
            <button v-on:click="startGame" v-if="ready">Lancer le jeu</button>
            
          </nav>
          <div>
            <h2>Commandes</h2>
            <p>z = haut</p>
            <p>q = gauche</p>
            <p>s = bas</p>
            <p>d = droite</p>
            <p>espace = pause</p>
          </div>

          <div>
            <h2>points:</h2>
            <p>{{points-3}}</p>
          </div>
        </aside>

        <table>
          <tbody></tbody>
        </table>
      <div class="filter" v-if="end">
      </div>
      <div class="modal" v-if="end">
        <h3>Game Over!</h3>
        <p>Vous avez fait {{points-3}} points</p>
        <button v-on:click="resetData">Terminer</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'game',
  data () { 
    return {
      ready: false,
      paused: false,
      end: false,
      borderCollision: false,
      gridSize: 0,
      direction: 'up',
      points: 3,
      col: 0,
      row: 0,
      speed: 0
    }
  },

  methods : {

    sendGridSize: function(){
      const Table = document.querySelector('table'),
      fragment = document.createDocumentFragment(),
      gridParse = parseInt(this.gridSize),
      middlePoint = gridParse/2+1,
      TBody = document.createElement('tbody')

      Table.removeChild(Table.firstChild)
      Table.appendChild(TBody)
      this.ready = true

      for( var i=0; i < gridParse+1; i++){
          const Tr = document.createElement('tr')
          TBody.appendChild(Tr)
          Tr.classList.add('item','item-col', 'item-col-'+(i+1))

        for( var j=0; j < gridParse+1; j++){
          const Td = document.createElement('td')
          Tr.appendChild(Td)
          Td.classList.add('item', 'item-row-'+(j+1))
        }
      }
      Table.appendChild(fragment)

      const start = document.querySelector('.item-col-'+middlePoint+'>'+'.item-row-'+middlePoint)
      start.classList.add('item-active')
      this.col = middlePoint
      this.row = middlePoint

      const borderTableTop = document.querySelector('.item-col-1').children,
      borderTableLeft = document.querySelectorAll('.item-row-1'),
      borderTableBottom = document.querySelector('.item-col-'+(gridParse+1)).children,
      borderTableRight = document.querySelectorAll('.item-row-'+(gridParse+1))
      function borders(){
        function addBorders(list){
          for(var i=0;i<list.length;i++){
            list[i].classList.add('border')
          }
        }
        addBorders(borderTableTop)
        addBorders(borderTableBottom)
        addBorders(borderTableLeft)
        addBorders(borderTableRight)
      }
      borders()

    },
    startGame: function(){
      const gridParse = parseInt(this.gridSize),
      middlePoint = gridParse/2+1,
      self = this
      let sneakHead = document.querySelector('.item-col-'+self.col+'>'+'.item-row-'+(self.row))

      const start = document.querySelector('.item-col-'+middlePoint+'>'+'.item-row-'+middlePoint)
      start.classList.remove('item-active')

      function addBlock(){
        const allItems = document.querySelectorAll('.item:not(.item-active):not(.border):not(.item-col)')
        const itemsNumber = allItems.length
        let itemRandom = Math.floor((Math.random() * itemsNumber-1) + 0);
        allItems[itemRandom].classList.add('item-block')
      }
      addBlock()

      setInterval(function(){

        let sneakBody = document.querySelectorAll('.item-active')

        function memorizePath(){

          sneakHead.setAttribute('data-num', 1)

          for(var i = 0; i<sneakBody.length; i++){
            
            if(sneakBody[i].hasAttribute('data-num')){        
              const dataNum =  parseInt(sneakBody[i].getAttribute('data-num'))
              sneakBody[i].setAttribute('data-num', dataNum+1)
            }
          }
          const itemsToSelect = Array.prototype.slice.call(document.querySelectorAll('[data-num]'))
          itemsToSelect.sort(function(a,b){
            return +a.getAttribute('data-num') - +b.getAttribute('data-num')
          })
          function overPointsLength(){
            return   itemsToSelect.length-self.points
          }
          const itemsToErrase = itemsToSelect.slice(-overPointsLength())
          if(itemsToSelect.length>self.points){
            for(var i = 0;i<itemsToErrase.length;i++){
              itemsToErrase[i].removeAttribute('data-num')
              itemsToErrase[i].classList.remove('item-active')
              itemsToErrase[i].classList.remove('item-body')
            }
          }
        }

        function searchCollision(){
          if(sneakHead.classList.contains('item-block')){
            self.points = self.points+1
            const foodBlock = document.querySelector('.item-block')
            foodBlock.classList.remove('item-block')
            addBlock()
          }
          if(sneakHead.classList.contains('item-body')){
            self.end = true
            self.paused = true
          }
        }

        function removeActiveHead(){
          for( var i=0; i < sneakBody.length; i++){
            if(sneakBody[i].getAttribute('data-num')>1){
              sneakBody[i].classList.remove('item-head')
              sneakBody[i].classList.add('item-body')
            }
          }     
        }

        searchCollision()

        function move(){
          if(self.col == 1 || self.col == gridParse+1 || self.row == 1 || self.row == gridParse+1){
              self.end = true
              self.paused = true
          }
          else{
            if(self.direction == 'up' && self.paused == false){
              sneakHead = document.querySelector('.item-col-'+(self.col-1)+'>'+'.item-row-'+self.row)
              sneakHead.classList.add('item-active', 'item-head')
              self.col = self.col-1
              memorizePath()
            }
            else if(self.direction == 'down' && self.paused == false){
              sneakHead = document.querySelector('.item-col-'+(self.col+1)+'>'+'.item-row-'+self.row)
              sneakHead.classList.add('item-active', 'item-head')
              self.col = self.col+1
              memorizePath()
            }
            else if(self.direction == 'left' && self.paused == false){
              sneakHead = document.querySelector('.item-col-'+self.col+'>'+'.item-row-'+(self.row-1))
              sneakHead.classList.add('item-active', 'item-head')
              self.row = self.row-1
              memorizePath()
            }
            else if(self.direction == 'right' && self.paused == false){
              sneakHead = document.querySelector('.item-col-'+self.col+'>'+'.item-row-'+(self.row+1))
              sneakHead.classList.add('item-active', 'item-head')
              self.row = self.row+1
              memorizePath()
            }
          }
        }
        removeActiveHead()
        move()
      }, self.speed)

      document.addEventListener('keydown', function (e){
          e.preventDefault();

          if(e.keyCode == 90 && self.direction !== 'down') {
                self.direction = 'up'
          }
          else if(e.keyCode == 81 && self.direction !== 'right') {
                self.direction = 'left'
          }
          else if(e.keyCode == 68 && self.direction !== 'left') {
                self.direction = 'right'
          }
          else if(e.keyCode == 83 && self.direction !== 'up') {
                self.direction = 'down'
          }
          else if(e.keyCode == 32){
            self.paused ^= true
            
          }
      })
    },
    resetData: function(){
      this.$router.go(0)
    }
  }
}
</script>

<style>

table{
  border-collapse: collapse;
  border: solid 2px rgb(82, 5, 5);
}

.item{
  width: 20px;
  height: 20px;
  box-sizing: border-box;
  background-color: rgb(4, 4, 37);
}

.item-active{
  background-color: rgb(134, 7, 7);
}
.item-block{
  background-color: rgb(196, 112, 112);
}
.border{
   visibility: hidden;
}
.modal{
  background-color: rgb(240, 203, 102);
  padding: 200px;
  position: absolute;
  z-index: 3;
  transform-origin: center;
  top: 20vh;
  left: 30%;
}
.filter{
  background-color: #fff;
  opacity: 0.8;
  position: absolute;
  z-index: 2;
  width: 100%;
  height: 100vh;
}

</style>



